<template>
    <div :class="getGrid(parseInt(props.option.depth)) + ' fields'">
        <div class="field"
             v-bind:class="{ required: props.option && props.option.required }"
             v-for="(component, index) in parseInt(props.option.depth)"
        >
            <label>{{ props.label }}</label>
            <select class="ui search dropdown" :name="'field'+props.sn+'_'+index" :id="'field'+props.sn+'_'+index" v-model="data[index]" @change="resetChild(index)">
                <option value="-1">{{ props.placeholder }}</option>
                <option :value="code.sn" v-if="index === 0" v-for="(code, codeIndex) in props.codes">{{ code.name }}</option>
                <option :value="opt.sn" v-if="opt.depth !== 0 && data[index - 1] && opt.parentSn === data[index - 1]" v-for="opt in opts">{{ opt.name }}</option>
            </select>
            <p class="postscript" v-if="props.postscript">{{ props.postscript }}</p>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'SelectTree',
        props: [
            'props',
            'grid'
        ],
        data: function () {
            return {
                count: 0,
                data: [],
                opts: []
            }
        },
        mounted() {
            const self = this;

            for(let i = 0; i < self.props.option.depth; i++) {
                self.data.push(-1);
            }

            self.getCodes(self.props.codes, 0, null);
        },
        methods: {
            getCodes(codes, depth, parentSn) {
                const self = this;

                if(codes === undefined) return;

                for(let i = 0; i < codes.length; i++) {
                    codes[i].depth = depth;

                    if(depth && parentSn !== undefined) {
                        codes[i].parentSn = parentSn;
                        self.opts.push(codes[i]);
                    }

                    if(codes[i].codes) {
                        self.getCodes(codes[i].codes, depth + 1, codes[i].sn);
                    }
                }
            },
            resetChild(index) {
                const self = this;

                if(self.data[index + 1] !== undefined) self.data[index + 1] = -1;
            }
        }
    }
</script>